let stationNames = [];
let stations = [];
realtimeArrivalSample.forEach(arrival => {
    if (stationNames.includes(arrival.STATION)) {
        const i = stationNames.indexOf(arrival.STATION);
        if (!stations[i].lines.includes(arrival.LINE)) {stations[i].lines.push(arrival.LINE)};
        if (!stations[i].dirs.includes(arrival.DIRECTION)) {stations[i].dirs.push(arrival.DIRECTION)};

    } else {
        stationNames.push(arrival.STATION);
        stations.push({
            name: arrival.STATION,
            lines: [arrival.LINE],
            dirs: [arrival.DIRECTION]
        });
    }

});

stationNames.sort();
stations.sort((a,b) => (a.name > b.name) ? 1 : ((b.name > a.name) ? -1 : 0));

realtimeArrivalSample.sort((a,b) => ((a.LINE + a.DIRECTION + a.STATION) > (b.LINE + b.DIRECTION + b.STATION)) ? 1
                                     : (((b.LINE + b.DIRECTION + b.STATION) > (a.LINE + a.DIRECTION + a.STATION)) ? -1 : 0));

export {realtimeArrivalSample, stationNames, stations};